
#toggle {
    display: none;
  }
  
  /**
    Hamburger
  **/
  .hamburger {
    position: fixed;
    bottom: 25px;
    right: 50px;
    z-index: 5;
    border-radius: 15px;
    background-color: #9427fb;;
  }
  
  .hamburger div {
    position: relative;
    width: 1.5em;
    height: 5px;
    border-radius: 3px;
    background-color: #000000;
    margin: 8px;
    transition: all 0.3s ease-in-out;
  }
  
  /**
  Nav Styles
  **/
  .burger-nav {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #9427fb;;
    top: -100%; left: 0; right: 0; bottom: 0;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
    transform: scale(0);
  }
  .nav-wrapper {
    position: relative;
    overflow: hidden;
    overflow-y: auto;
    height: 100%;
  }
  .nav-burger-nav {
    text-align: left;
    margin-left: 25%;
  }
  .nav-burger-nav a {
    position: relative;
    text-decoration: none;
    color: #232323;
    font-size: 2em;
    display: inline-block;
    margin-top: 1.25em;
    transition: color 0.2s ease-in-out;
    letter-spacing: 1px;
  }
  .nav-burger-nav a:before {
    content: '';
    height: 0;
    position: absolute;
    width: 0.25em;
    background-color: white;
    left: -0.5em;
    transition: all 0.2s ease-in-out;
  }
  .nav-burger-nav a:hover {
    color: white;
  }
  .nav-burger-nav a:hover:before {
    height: 100%;
  }
  
  /**
  Animations
  **/
  #toggle:checked + .hamburger .top-bun {
    transform: rotate(-45deg);
    margin-top: 25px;
  }
  #toggle:checked + .hamburger .bottom-bun {
    opacity: 0;
    transform: rotate(45deg);
  }
  #toggle:checked + .hamburger .meat {
    transform: rotate(45deg);
    margin-top: -7px;
  }
  
  #toggle:checked + .hamburger + .burger-nav {
    top: 0;
    transform: scale(1);
  }